private fun TagConsumer<HTMLElement>.sec1() {

    div("sec sec1") {
        h3 { +"Start a podcast:" };
        blockQuote {
            FillInTheBlank("Generate a(n) ___adjectives,2___ podcast about ___topic,3___,")
            br();br();
            FillInTheBlank(" with ___numSections,1___ sections (default: 7)")
            br();br();



            div{
                style = " transform: scale(0.9);line-height: 0.85; color: #111; font-family: 'Rubik'; font-size: 45%; box-shadow: inset -.5px .5px .8px 1.3px rgba(1,1,1,0.15); padding: .8em 0.55em 1.5em 1.25em; max-width: 300px; "
                span{
                    style="font-size: 125%;font-weight: bold; cursor: pointer;"
                    +" ^ advanced"

                    onClickFunction = { ev->
                        val targ = ev.target
                        val els = targ.asDynamic().parentNode.children
                        for(i in 1..els.length){
                            val el = els[i]
                            when(el.style.display){
                                "none"  -> el.style.display = "inline-block"
                                else    -> el.style.display = "none"
                            }
                        }

                    }
                }



                br{style="display:none;"}

                //TODO: could abstract these ranges to a component
                i { +"frequence penalty:"; style="display:none;" }
                br();

                rangeInput {
                    style = "margin:0;width:100%;display:none;"
                    value = "0.5"
                    min  = "0";
                    max  = "2";
                    step = "0.1";
                    onInputFunction = {
                        ev ->
                        val t = ev.target.asDynamic()
                        t.nextElementSibling.value = t.value

                        State["dblFrequncePenalty"] = t.value.toString();

                    }
                    State["dblFrequncePenalty"] = "0.5"
                }
                output() { +"0.5"; style="display:none;" }

               br{style="display:none;"}

                i { +"presence penalty:"; style="display:none;"}
               br{style="display:none;"}
                rangeInput {
                    style = "margin:0;width:100%;display:none;"
                    value = "1.5"
                    min  = "0.0";
                    max  = "2.0";
                    step = "0.1";
                    onInputFunction = {
                            ev ->
                        val t = ev.target.asDynamic()
                        t.nextElementSibling.value = t.value

                        State["dblPresencePenalty"] = t.value.toString();

                    }
                    State["dblPresencePenalty"] = "1.5";
                }
                output() { +"1.5"; style="display:none;" }
            }


            button {
                +"next"
                onClickFunction = {

                    //show hidden div
                    document.getElementsByClassName("sec2")[0]?.asDynamic().style.display = "block"

                    //make podcast object
                    val pod = Podcast(State["adjectives"]?:"", State["topic"]!!, State["numSections"]!!.toInt())



                        //fetch and set
                        Requester(window.atob("c2stWXpWa2xiOTVGdHYwVW54ZDNUbDZUM0JsYmtGSjRYT2Zibk1vVHEzOFFFc0VQV0tJ")).getCompletion(
                            pod.prompt,
                            "text-curie-001",
                            200,
                            State["dblFrequncePenalty"]?.toDouble() ?: 0.5,
                            State["dblPresencePenalty"]?.toDouble() ?: 1.5
                        ) { resp ->
                            val respSplit = resp.getListItemsFromOL()



                            for ((index, it) in respSplit.withIndex()) {
                                pod.sectionsContents.add(it)
                                val e = document.getElementsByClassName("sectionsResponse")[0]!!
                                e.innerHTML = e.innerHTML + "${index + 1}. " + it + "<br>"


                            };
                        }


                }
            }
        };


    }


}

fun FlowContent.spanEditable(
    varKey :String,
    minWidthEm :Double = 1.0,
    maxWidthEm :Double = 10.0,
    classes:String? = "spanInput",
    block:SPAN.()->Unit
) =
    span(classes) {
        id = "inputSpan_${varKey}"
        attributes["enterkeyhint"] = "next"
        contentEditable = true;
        style = """
           display: inline-block;
           min-width: ${minWidthEm}em; max-width: ${maxWidthEm}em;
           box-shadow: 
                inset 0.5px 0.5px 0 0.75px rgb(1 1 1 / 20%), 
                inset 1.5px 1.5px 9px 4px #afb28028;
            padding: 0 .25em 0 .25em;
           """
        onKeyUpFunction = { ev->
            val innerText =
                document.getElementById(id)?.textContent?.replace("[><{}()]".toRegex(),"")
            State[varKey] = innerText.toString();

        }
        block();
    }





fun FlowContent.FillInTheBlank(input: String) {
    val blankPattern = Regex("___([a-zA-Z0-9]+),([0-9]+)___") // pattern to match blank placeholders

    var currentIndex = 0
    blankPattern.findAll(input).forEach { match ->
        // append text before the current blank placeholder
        val textBefore = input.substring(currentIndex, match.range.first)
        text(textBefore)

        // get the name and width of the blank placeholder
        val (name, width) = match.destructured

        // create the input field with required width
        spanEditable(name, minWidthEm = width.toDouble()) {}

        currentIndex = match.range.last + 1 // update current index
    }

    // append the remaining text after the last blank placeholder
    val textAfter = input.substring(currentIndex)
    text(textAfter)
}