package misc


import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.async
import kotlinx.coroutines.await
import kotlinx.coroutines.promise
import org.w3c.dom.get
import org.w3c.fetch.Headers
import org.w3c.fetch.RequestInit
import kotlin.js.Promise
import kotlin.js.json

class Requester(
    val apiKey :String
) {

    fun getCompletion(
        prompt :String,
        model :String = "text-davinci-003",
        maxTokens :Int = 256,
        frequencyPenalty :Double = 0.45,
        presencePenalty :Double = 1.6,
        thenBlock :(resp:String)->Unit = {},
    ): Unit {


        val bodyRaw = json(
                "model" to model,
                "prompt" to prompt,
                "temperature" to 0.75,
                "max_tokens" to maxTokens,
                "top_p" to 1,
                "frequency_penalty" to frequencyPenalty,
                "presence_penalty" to presencePenalty,
                "stop" to arrayOf("""\n\n""")
            ).toJsonString()

        val requestOptions = RequestInit(
            method = "POST",
            headers = Headers().apply {
                append("Content-Type", "application/json")
                append("Authorization", "Bearer ${apiKey}")
            },
            body = bodyRaw,
        )

        var resultStr :String? = null;

        GlobalScope.async {

              val response = window.fetch("https://api.openai.com/v1/completions", requestOptions)
              val responseStr = response.await().text().await()
              console.log("response received:\n$responseStr")
              ;
              val choiceresult = (JSON.parse<String?>(responseStr).asDynamic()).choices[0].text

               console.log("in requester: \n${choiceresult}")

              thenBlock(choiceresult.toString());


           }





    }

}