package misc



object State {
   val inputVars :MutableList<InputVar> = mutableListOf()

   operator fun get(key: String): String? =
      inputVars.find { it.key == key }?.value ?: null

   operator fun set(key: String, value: String) {
      var inputVar =
         inputVars.find { it.key == key }

      if(inputVar == null){
         inputVar = InputVar(key)

         inputVars.add(inputVar)
      }

      inputVar.value = value
   }


   override fun toString(): String {
      var o = "Store:\n"
      inputVars.forEach { o += "_$${it.key}: \t${it.value}\n" }
      return o;
   }
}

data class InputVar(
   val key     :String,
   var value   :String? = null
)